<template>
  <v-container class="d-flex flex-column px-0" app>
    <v-app-bar fixed class="top" app color="white">
      <v-btn fab text small @click="$router.back(-1)" class="mt-2">
        <v-icon large>mdi-arrow-left</v-icon>
      </v-btn>
      <span class="text_middle mt-2">系统消息</span>
    </v-app-bar>
    <div class="container_systemMessage" v-touch:swiperight="rightSlide">
      <v-tabs color="primary" grow>
        <v-tab>删除消息</v-tab>
        <v-tab>标签修改</v-tab>
        <v-tab>禁言消息</v-tab>
        <v-tab-item>
          <div v-if="deleteMessage.length==0" class="d-flex justify-center mt-2">
            <v-icon color="primary">mdi-emoticon-cool-outline</v-icon>
            <span class="ml-1">暂无消息</span>
          </div>
          <v-list v-else>
            <div v-for="(item,i) in deleteMessage" :key="i">
              <v-list-item>
                <v-list-item-avatar>
                  <v-icon color="primary">mdi-message-text</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>系统消息 {{item.type}}处理#{{item.id}}</v-list-item-title>
                  <div class="d-flex flex-column">
                    <span class="line_one grey--text text_small--">您发表的"{{item.content}}"</span>
                    <span
                      class="grey--text text_small--"
                    >因为违反社区规范【五.{{item.reason}}】：发表“{{processType[item.reason]}}”被删除</span>
                  </div>
                </v-list-item-content>
                <v-list-item-action>
                  <v-list-item-action-text>{{item.add_time.slice(5,10)}}</v-list-item-action-text>
                </v-list-item-action>
              </v-list-item>
              <v-divider></v-divider>
            </div>
          </v-list>
        </v-tab-item>
        <v-tab-item>
          <div v-if="changeMessage.length==0" class="d-flex justify-center mt-2">
            <v-icon color="primary">mdi-emoticon-cool-outline</v-icon>
            <span class="ml-1">暂无消息</span>
          </div>
          <v-list v-else>
            <div v-for="(item,i) in changeMessage" :key="i">
              <v-list-item>
                <v-list-item-avatar>
                  <v-icon color="primary">mdi-message-text</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>系统消息 {{item.type}}处理#{{item.id}}</v-list-item-title>
                  <div class="d-flex flex-column">
                    <span class="line_one grey--text text_small--">您发表的"{{item.content}}"</span>
                    <span class="grey--text text_small--">因为内容包含{{item.tag}}被管理员加上了{{item.tag}}标签</span>
                  </div>
                </v-list-item-content>
                <v-list-item-action>
                  <v-list-item-action-text>{{item.add_time.slice(5,10)}}</v-list-item-action-text>
                </v-list-item-action>
              </v-list-item>
              <v-divider></v-divider>
            </div>
          </v-list>
        </v-tab-item>
        <v-tab-item>
          <div v-if="processMessage.length==0" class="d-flex justify-center mt-2">
            <v-icon color="primary">mdi-emoticon-cool-outline</v-icon>
            <span class="ml-1">暂无消息</span>
          </div>
          <v-list>
            <div v-for="(item,i) in processMessage " :key="i">
              <v-list-item>
                <v-list-item-avatar>
                  <v-icon color="primary">mdi-message-text</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>系统消息 处理#{{item.id}}</v-list-item-title>
                  <div class="d-flex flex-column">
                    <span
                      class="grey--text text_small--"
                    >因为{{item.type}},您将被禁言到{{item.end_time.slice(0,16).replace('T',' ')}}</span>
                  </div>
                </v-list-item-content>
                <v-list-item-action>
                  <v-list-item-action-text>{{item.add_time.slice(5,10)}}</v-list-item-action-text>
                </v-list-item-action>
              </v-list-item>
              <v-divider></v-divider>
            </div>
          </v-list>
        </v-tab-item>
      </v-tabs>
    </div>
    <v-dialog
      v-model="processExplainShow"
      scrollable
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <v-icon>mdi-help-circle</v-icon>
          <span class="ml-2">关于</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-1">
          <div v-html="processExplainContent"></div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-btn color="primary" fab large bottom right fixed @click="processExplainShow=true">
      <v-icon large>mdi-help</v-icon>
    </v-btn>
  </v-container>
</template>

<script>
const processExplainContent= `<div style="margin-left:-10px"> <ul> <li> <strong><span>删帖规则</span></strong><br> <span>请浏览官网-社区规范 </span><a href="http://scuhuoguodiliao.com/#/rules" style="color:red;text-decoration:none"><strong><span>『火锅底料』社区规范</span></strong></a><span>。</span><br> </li> <li> <strong><span>标签修改</span></strong><br> <span>管理员会对一些带有“性相关”、“令人不适”、“政治相关”、“未经证实”、“引战”内容并且没有加标签的帖子加上对应标签</span><br> </li> <li> <strong><span>禁言规则</span></strong><br> <span>若用户发表的内容根据社区规范-(五)而被删除，处罚为“（历史删帖总数+历史删评总数）×7（天）”的禁言。</span><br> <span>若在禁言期间有另一内容被删除，禁言时间更新。</span><br> <span>累计删帖删评次数在每年的1月1日清零。</span><br> <span>若用户发表带有“性相关”、“令人不适”、“政治相关”、“未经证实”、“引战”的内容，并且没有加标签，由管理员加上了，现行规则下处罚为一天禁言</span><br> &nbsp;<br> </li> </ul> </div>`

import touch from '../../utils/touch'
export default {
  name: 'systemMessage',
  data() {
    return {
      processType: {
        1: '违反《中华人民共和国宪法》等(一)中规定的法律法规的内容',
        2: '侮辱或诽谤他人或特定群体的内容',
        3: '侵犯他人隐私的内容',
        4: '侵犯他人知识产权、商业机密的内容',
        5: '散布谣言和恐慌，可能会误导他人的内容',
        6: '含有任何淫秽、色情、性暗示、不当暴露的内容',
        7: '鼓励、教唆他人实施违法犯罪、暴力、非法集会的内容',
        8: '含有隐喻、容易引起歧义的缩写、代名词、特定表述等对于政治等敏感问题进行引导、讨论',
        9: '预留',
        10: '含有自杀/自残倾向性的内容',
        11: '干扰社区正常运营，大量重复内容垃圾发帖等',
        12: '引起他人不适的内容',
        13: '经本人要求删除涉及个人隐私、过多细节或对本人生活造成不良影响的本人发帖'
      },
      deleteMessage: [],
      processMessage: [],
      changeMessage: [],
      processExplainShow: false,
      processExplainContent:sessionStorage.getItem('processExplainContent')||processExplainContent

    }
  },
  created() {
    if (sessionStorage.getItem('SystemMessageNumTemp'))
      localStorage.setItem('SystemMessageNum', sessionStorage.getItem('SystemMessageNumTemp'))
    let that = this
    that.$request('get', '/user/getSystemMessage', {}, {})
      .then(res => {
        res = res.data
        that.processMessage = res.processMessage;
        that.deleteMessage = res.deleteMessage;
        that.changeMessage = res.changeMessage;
      })
      .catch(e => {
        that.showSnackbar(e)
      })
  },
  methods: {
    rightSlide() {
      this.$router.go(-1)
    }
  },
  directives: {
    touch
  },
  watch: {
    'processExplainShow'(v) {
      this.$store.change_dialog(v, 'processExplainShow');
    },
    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'processExplainShow')
        this.processExplainShow = v
    }
  }
}
</script>

<style>
.container_systemMessage {
  margin-top: 46px;
}
</style>


